import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WelcomeComponent } from "../welcome/welcome.component";
import { MathClassComponent } from "../math-class/math-class.component";
import { PricingComponent } from "../pricing/pricing.component"
import { TeachersComponent } from "../teachers/teachers.component";
import {ScienceClassComponent} from "../science-class/science-class.component";
import {LiteratureClassComponent} from "../literature-class/literature-class.component";
import {ComputerScienceClassComponent} from "../computer-science-class/computer-science-class.component";
import {FinanceClassComponent} from "../finance-class/finance-class.component";
import {HistoryClassComponent} from "../history-class/history-class.component";
import {MathClassFrontComponent} from "../math-class-front/math-class-front.component";
import {ScheduleComponent} from "../schedule/schedule.component";
import {VolunteerComponent} from "../volunteer/volunteer.component";


const routes: Routes = [
  { path: '', component: WelcomeComponent },
  { path: 'pricing', component: PricingComponent },
  { path: 'teachers', component: TeachersComponent },
  { path: 'class/1', component: MathClassFrontComponent },
  { path: 'class/2', component: ScienceClassComponent },
  { path: 'class/3', component: LiteratureClassComponent },
  { path: 'class/4', component: ComputerScienceClassComponent },
  { path: 'class/5', component: FinanceClassComponent },
  { path: 'class/6', component: HistoryClassComponent },
  { path: 'schedule', component: ScheduleComponent},
  { path: 'volunteer', component: VolunteerComponent },
  { path: '**', redirectTo: '/'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class RouteRoutingModule { }
